
.hover-highlight {
  -webkit-transition: 0.2s ease color;
  transition: 0.2s ease color;
}
.hover-highlight:hover {
    color: #FC7753;
}
span.image-caption {
  display: block;
  text-align: center;
  font-size: 90%;
  color: grey;
}
section.splash {
  width: 100vw;
  height: 100vh;
}
section.splash .shadow {
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
}
section.splash .body {
    background-image: url(../assets/background.jpg);
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
}
section.splash .content {
    font-family: "Comfortaa", "source sans pro", "Ubuntu", "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Source Han Sans CN", "Source Han Sans SC", "Microsoft YaHei", "Wenquanyi Micro Hei", "WenQuanYi Zen Hei", "ST Heiti", SimHei, "WenQuanYi Zen Hei Sharp", "Arial", sans-serif;
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
section.splash .content .title {
      margin-bottom: 1.4rem;
      color: rgba(255, 255, 255, 0.8);
      font-size: 6rem;
}
section.splash .content .subtitle {
      margin-bottom: 1rem;
      color: rgba(255, 255, 255, 0.7);
}
section.splash .content nav {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
@media only screen and (max-width: 600px) {
section.splash .content nav {
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
              -ms-flex-direction: column;
                  flex-direction: column;
}
}
section.splash .content nav a {
        color: white;
        font-size: 1.8em;
        padding: 0 0.6em 1em;
        color: rgba(255, 255, 255, 0.65);
        -webkit-transition: all 0.6s;
        transition: all 0.6s;
        cursor: pointer;
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
}
section.splash .content nav a:hover {
          color: white;
          text-shadow: 0 0 2px #fff;
}



