
.hover-highlight {
  -webkit-transition: 0.2s ease color;
  transition: 0.2s ease color;
}
.hover-highlight:hover {
    color: #FC7753;
}
span.image-caption {
  display: block;
  text-align: center;
  font-size: 90%;
  color: grey;
}
.tags-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.tags-container .tag {
    border: 1px solid #8B80F9;
    -webkit-box-shadow: 0px 0px 0.2em #8B80F9;
            box-shadow: 0px 0px 0.2em #8B80F9;
    border-radius: 2em;
    padding: 0 1em;
    margin-top: 0.5em;
    margin-right: 0.5em;
    cursor: pointer;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
    display: inline-block;
}
.tags-container .tag:last-child {
      margin-right: 0;
}
.tags-container .tag:hover, .tags-container .tag.active {
      border: 1px solid #FC7753;
      -webkit-box-shadow: 0px 0px 0.2em #FC7753;
              box-shadow: 0px 0px 0.2em #FC7753;
}
.tags-container .tag.active {
      color: #FC7753;
}



