
.hover-highlight, .blog-item .title, .blog-item .tag {
  -webkit-transition: 0.2s ease color;
  transition: 0.2s ease color;
}
.hover-highlight:hover, .blog-item .title:hover, .blog-item .tag:hover {
    color: #FC7753;
}
span.image-caption {
  display: block;
  text-align: center;
  font-size: 90%;
  color: grey;
}
.blog-item {
  margin-bottom: 2em;
}
.blog-item .title {
    margin-bottom: 0.5rem;
    color: #8B80F9;
    cursor: pointer;
}
.blog-item .time {
    color: #606c76;
}
.blog-item .tag {
    color: #606c76;
    cursor: pointer;
    padding-left: 0.4em;
}



