
.hover-highlight {
  -webkit-transition: 0.2s ease color;
  transition: 0.2s ease color;
}
.hover-highlight:hover {
    color: #FC7753;
}
span.image-caption {
  display: block;
  text-align: center;
  font-size: 90%;
  color: grey;
}
.nav-container {
  position: fixed;
  z-index: 100;
}
.nav-container .entry {
    background-color: rgba(96, 108, 118, 0.3);
    margin-top: 1em;
    font-size: 3rem;
    width: 4rem;
    height: 4rem;
    -webkit-transition: 0.4s ease;
    transition: 0.4s ease;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    top: 0;
    left: 0;
    position: fixed;
    z-index: -1;
    transition: 0.4s ease;
}
.nav-container .entry:hover, .nav-container .entry.active {
      background-color: #606c76;
      color: white;
}
.nav-container .entry.active:hover {
      color: #FC7753;
}
.nav-container .nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    display: flex;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100vw;
    height: 100vh;
}
.nav-container .nav .item {
      font-size: 4rem;
      font-family: "Comfortaa", "source sans pro", "Ubuntu", "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Source Han Sans CN", "Source Han Sans SC", "Microsoft YaHei", "Wenquanyi Micro Hei", "WenQuanYi Zen Hei", "ST Heiti", SimHei, "WenQuanYi Zen Hei Sharp", "Arial", sans-serif;
      cursor: pointer;
      padding-left: 1em;
      padding-right: 1em;
      color: rgba(255, 255, 255, 0.8);
}
@media only screen and (max-height: 400px) {
.nav-container .nav .item {
          font-size: 3rem;
}
}
.nav-container .nav .item:hover {
        color: white;
        text-shadow: 0 0 2px #fff;
}



